import * as React from "react";
import style from "./FreeToPlay.module.scss"
import {H2} from "../D_Common/H2/H2";

import "swiper/css";
import SwiperClass from 'swiper/types/swiper-class';
import {Swiper, SwiperSlide} from "swiper/react";
import {useRef, useState} from "react";
import {svgIcons} from "../../assets/svg/svgIcons";
import clsx from "clsx";
import {AnimatedCircle} from "../D_Common/AnimatedCircle/AnimatedCircle";
import {useAppSelector} from "../../store/hooks";
import {selectX, selectY} from "../../store/appSlice";

const slides = [
    "Resources Gathering",
    "World Exploration",
    "NFT Trading",
    "Faction VS Faction",
    "Guild VS Guild",
    "PVP",
    "PVE",
    "Base building",
];


export const FreeToPlay = () => {
    //const [swiper, setSwiper] = useState<SwiperClass | null>(null);
    const [currentIndex, setCurrentIndex] = useState(0);

    const x = useAppSelector(selectX);
    const y = useAppSelector(selectY);

    const ref = useRef<HTMLDivElement>(null);

    return (
        <section className={style.freeToPlay}>
            <H2 titles={["Free to Play,", "Play to Earn"]}/>



            <div className={style.content} ref={ref}>

                <div className={style.gradientMask}
                     style={{
                         top: `calc(${y - 2500 - (ref?.current?.getBoundingClientRect().top ?? 0) - window.pageYOffset}px)`,
                         left: `${x - 2500}px`,
                     }}
                />

                <Swiper slidesPerView="auto"
                        slidesPerGroup={1}
                        centeredSlides={true}
                        // onSwiper={(swiper) => {
                        //     setSwiper(swiper);
                        // }}
                        onSlideChange={swiper => setCurrentIndex(swiper.realIndex)}
                        className={style.swiperMobile}
                        spaceBetween={10}
                >
                    {
                        slides.map((slide, index) => (
                            <SwiperSlide className={clsx({
                                [style.slide]: true,
                                [style.slide_active]: index === currentIndex,
                            })}
                                         key={index}

                            >
                                <div className={style.slideInnerWrapper}>
                                    <div className={style.circleIcon}>{svgIcons.circle}</div>

                                    <p className={style.text}>{slide}</p>

                                    <div className={style.arrowIcon}>
                                        {svgIcons.bigArrow}
                                    </div>
                                </div>

                            </SwiperSlide>
                        ))
                    }
                </Swiper>

                <div className={style.circlesBlock}>
                    {
                        slides.map((text, index) => (
                            <div className={style.circleWrapper}
                                 key={index}
                            >
                                <AnimatedCircle className={style.circleIcon}/>

                                <p className={style.text}>{text}</p>

                                <div className={style.arrowIconWrapper}>
                                    {svgIcons.bigArrow}
                                </div>

                            </div>
                        ))
                    }
                </div>

                <div className={style.tokenBlock}>
                    <p>t</p>
                    <div className={style.tokenIcon}>{svgIcons.token}</div>
                    <p>ken</p>
                </div>

            </div>
        </section>
    )
}